<template>
  <div>
  	<div class="jujiao">
  		<i class="iconfont icon-jujiao-fangda">

      </i>
  		<p></p>
  	</div>

	<div class="picload-input-wrap">
      <a class="a-upload">
        <i class="iconfont icon-paizhao"></i>
        <input class="file" name="file" type="file" capture="camera" @change="upload($event)" accept="image/png,image/gif,image/jpeg"/>

      </a>
      <a style="background: #9E9E9E;" class="a-upload" @click="$router.go(-1)">
      	<i class="iconfont icon-close"></i>
      </a>
    </div>
    <loadpanel :showload="showload" :loadtext="loadtext"></loadpanel>
    <myalert :isshowalert="isshowalert" :alertmsg="alertmsg" :doclose="doclose"></myalert>

  </div>
</template>
<script>

    export default {
        components:{

        },
      data()
      {
          return {
            isshowalert:false,
            alertmsg:"",
            showload: false,
            loadtext: "",
            courseNo:'',
            courseType:'',
          }
      },
      mounted()
      {
        this.courseNo=this.$route.query.courseNo;
        this.courseType=this.$route.query.courseType?this.$route.query.courseType:'';
      },
      methods:{
        doclose()
        {
          this.isshowalert=false;
        },
        upload(e)
        {
            let file=e.target.files[0];
            let param=new FormData();
            let that = this;

          var reader = new FileReader();
          reader.readAsDataURL(file);
          reader.onload = function(e){
            param.append('uploadFile64',this.result.split("base64,")[1]);
            param.append('courseType',that.courseType);

            let config={
              hearder:{'Content-Type':'multipart/form-data'}
            }
            that.showload=true;
            that.axios.post(baseurl+"/api/vedio/uploadImage.html",param,config).then(
              response=>{
                that.showload=false;
                console.log(response);
                if(response.data.success)
                {
                  that.$router.push({path:'/vedio/list',query:{courseNo:that.courseNo,courseType:that.courseType}});
                }else
                {
                  that.alertmsg='';
                  that.isshowalert=true;
                }
              },
              response=>{
                that.showload=false;
                that.alertmsg='';
                that.isshowalert=true;
                that.$router.go(-1);
              }
            );
          }
        }
      }
    }
</script>
<style type="text/css">
	.jujiao{
		padding: 10rem 1rem 0;
		text-align: center;
	}
	.jujiao i{
		font-size: 8rem;
		color: #9E9E9E;
		font-style: normal;
	}
	.jujiao p{
		color: #999;
		font-size: 1rem;
		margin-top: -1.6rem;
	}



	 .picload-input-wrap {
    padding: 0.8rem 1rem;
    border-top: 1px solid #e5e5e5;
    text-align: center;
    position: fixed;
    bottom: 0;
    left: 0;
    right: 0;
  }

  .a-upload {
    margin-top: 0.2rem;
    margin-right: 0.8rem;
    margin-left: 0.8rem;
    text-align: center;
    /*padding: 1.4rem 1.4rem;*/
    height: 4rem;
    width: 4rem;
    line-height: 4rem;
    position: relative;
    cursor: pointer;
    color: #888;
    background: #F44336;
    /*border: 1px solid #F44336;*/
    border-radius: 4rem;
    overflow: hidden;
    display: inline-block;
    *display: inline;
    *zoom: 1
  }

  .a-upload input {
    position: absolute;
    left: 0;
    top: 0;
    opacity: 0;
    width: 3rem;
    height: 3rem;
    filter: alpha(opacity=0);
    cursor: pointer;
    background: #fff;
    border: none;
  }

  .a-upload i {
    color: #999;
    font-size: 2rem;
    position: absolute;
    color: #fff;
    top: 0rem;
    left: 1rem;
  }


</style>



